import Link from 'next/link'
import { useRouter } from 'next/router'

const menuStyle = {
  height: '60px',
  color: '#a8a8a8',
  'border-top': '1px solid #dedede',
  'background-color': 'white',
  position: 'fixed',
  bottom: 0
}

export default function Menu(props) {
  const router = useRouter()
  const activeMenu = (id) => {
    return (router.query.case === id) ? ' active' : ''
  }
  console.log(router)
  return (
    <div className="wrapper" style={menuStyle}>
      <div className="container menubar">
        <div className="menuitem">
          <Link className={activeMenu(1)} href='/?case=1'>
            Case 1
          </Link>
        </div>
        <div className="menuitem">
          <Link className={activeMenu(2)} href='/?case=2'>
            Case 2
          </Link>
        </div>
        <div className="menuitem">
          <Link className={activeMenu(3)} href='/?case=3'>
            Case 3
          </Link>
        </div>
        <div className="menuitem">
          <Link className={activeMenu(4)} href='/?case=4'>
            Case 4
          </Link>
        </div>
      </div>
      <style jsx>{`
        .menubar {
          align-items: center;
          padding: 0 14px;
          justify-content: space-around;
        }
        .menuitem {
          width: 80px;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #00aaef;
          cursor: pointer;
        }
        .active {
          color: rgb(168, 168, 168);
        }
      `}</style>
    </div>
  )
}